export default {
	data() {
		return {
			allOrder:[{
				status:"finish",
				orderNumber:'7564893587941546',
				img:'http://m.qpic.cn/psb?/V11CTl4R4bvwTh/35ChgGDxVYkFPqfiLVZyzm9Rme4AB10Sn2SfSGgC69s!/b/dL8AAAAAAAAA&bo=tAC0AAAAAAADFzI!&rf=viewer_4',
				title:"泰国天然乳胶枕头一对",
				material:"天然乳胶",
				price:228,
				num:1,
			},{
				status:"unfinish",
				orderNumber:'7564893587941546',
				img:'http://m.qpic.cn/psb?/V11CTl4R4bvwTh/35ChgGDxVYkFPqfiLVZyzm9Rme4AB10Sn2SfSGgC69s!/b/dL8AAAAAAAAA&bo=tAC0AAAAAAADFzI!&rf=viewer_4',
				title:"泰国天然乳胶枕头一对",
				material:"天然乳胶",
				price:228,
				num:1,
			},{
				status:"unfinish",
				orderNumber:'7564893587941546',
				img:'http://m.qpic.cn/psb?/V11CTl4R4bvwTh/35ChgGDxVYkFPqfiLVZyzm9Rme4AB10Sn2SfSGgC69s!/b/dL8AAAAAAAAA&bo=tAC0AAAAAAADFzI!&rf=viewer_4',
				title:"泰国天然乳胶枕头一对",
				material:"天然乳胶",
				price:228,
				num:2,
			}]
		}
	},
	methods: {
		// 删除订单
		del(e){
			var that=this
			wx.showModal({
				title:'确定删除该订单吗？',
				success (res) {
				    if (res.confirm) {
						that.allOrder.splice(e.currentTarget.dataset.index,1)
				    } else if (res.cancel) {
						console.log('用户点击取消')
				    }
				 }
			})
		},
		// 申请退款
		gotoRefund(e){
			var obj=this.allOrder[e.currentTarget.dataset.index]
			wx.navigateTo({
				url:"/pages/appref/appref?item="+JSON.stringify(obj)
			})
		}
	}
}
